header{
    background-color: white;
    height: 130px;
}
.homeHeader,
.loginHeader{
    background-color: white;
}

.loginButton{
    height: 33px;
    width: 100px;
    margin: auto 49px;
    border: none;
    border-radius: 10px;
    background-color: #D9D9D9;
}

header > div {
    display: flex;
    flex-direction: row;
}

header > div > span{
    width: 10%;
}

header > div > label {
    display: flex;
    flex-direction: column;
    flex-flow: column;
    width: 10%;
}

.menus {
    display: flex;
    flex-flow: row;
    width: 80%;
    justify-content: end;
    text-align: center;
}

.menus > div{
    margin: 42px 16px 38px 17px;
}

.menus label {
    display: flex;
    flex-direction: column;
    flex-flow: column;
}

li {
    list-style-type: none;
    padding: 0;
}

ul {
    padding: 0;
}

.homeHeader{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.logo{
    width: 130px;
    height: 130px;
}
.logo.userLogo{
    width: 100px;
    height: 100px;
    margin-top: 12px;
}

.dropdownContent{
    display: none;
    position: absolute;
    z-index: 1;
    width:max-content;
    height: auto;
    text-align: center;
    margin: 10px auto;
    background-color: white;
}

.dropdownContent > div{
    height: max-content;
    padding: 5px;
    margin: auto;
}

.dropdownContent > div:hover{
    font-weight: bold;
}

.dropdown{
    position: relative;
    display: inline-block;
    width: 180px;
    /* margin: auto; */
    height: 65px;
}

.dropdown > div {
    width: 180px;
    /* height: 50px; */
    border: 1px black solid ;
    border-radius: 10px;
}

.dropdown > img {
    display: flex;
    margin: auto;
}

.dropdown.userMenu{
    height: auto;
}

.dropdown:hover > .dropdownContent{
    display: block;
}

.menus .dropdown>div:first-of-type{
    width: 180px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}