html,
body,
#root{
    height: 100%;
    z-index: 0;
    margin: 0 20px;
}

.body{
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
}

header{
    position: sticky;
    top: 0;
    width: 100%;
}

main{
    flex: 1 0 auto;
    box-sizing: border-box;
    margin: 0;
}

footer{
    position: sticky;
    flex-shrink: 0;
    bottom: 0;
    text-align: center;
    background-color: white;
}