.setUserData{
    display: flex;
    flex-direction: row;
}

.setUserData>div{
    display: flex;
    flex-direction: column;
}

.setUserData>div:first-of-type{
    margin-right: 100px;
}

form{
    display: flex;
    flex-direction: column;
    margin: auto auto;
    width: max-content;
    height: max-content;
}

input,
form>button{
    border-color: black;
    border-style: solid;
    border-radius: 10px;
    margin-bottom: 26px;
    text-align: center;
    width: 295px;
    height: 50px;
    font-size: 16px
}

input{
    border-width: 1.5px;
}

input::placeholder{
    font-size: 14px;
}

form > button{
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    color: black;
    border-width: 1px;
}

form > button:hover{
    border-width: 3px;
}