.dadosPessoais{
    display: flex;
    flex-direction: column;
    margin: auto;
    width: max-content;
}
.dadosPessoais>div{
    display: flex;
    flex-direction: row;
}

.dadosPessoais>div>div{
    display: flex;
    flex-direction: column;
}
.dadosPessoais>div>div:first-of-type{
    margin-right: 20px;
}

.dadosPessoais span,
h2{
    border-bottom: 1px solid gray;
    padding-bottom: 15px;
    margin-top: 15px;
    min-width: 250px;
}

#searchUser{
    margin: 20px auto;
}

#searchForm{
    display: flex;
    flex-direction: row;
}

#searchForm > div,
#searchForm > div > input{
    margin: auto;
}
#searchForm > div > input{
    margin-right: 20px;
}

#searchForm button{
    width: fit-content;
    margin: auto;
}
